import React, { useState } from 'react';
import * as S from './App.styles';
import title from './images/title.gif';
import cat1 from './images/cat1.gif';
import cat2 from './images/cat2.gif';
import cat3 from './images/cat3.gif';
import ezhik from './images/ezhik.jpg';
import megahorosh from './images/megahorosh.gif';

const App = () => {
  const [isClicked, setClicked] = useState(false);

  const handleClick = () => {
    setClicked(!isClicked);
  };

  return (
    <S.Section onClick={() => isClicked && setClicked(false)}>
      <S.Container>
        <S.Title isClicked={isClicked}>
          <img src={title} alt='Title' />
        </S.Title>
        {isClicked && (
          <S.HoroshWrapper>
            <img src={megahorosh} alt='Mega horosh' />
          </S.HoroshWrapper>
        )}
        <S.Wrapper isClicked={isClicked}>
          <S.ImagesWrapper>
            <S.ImgWrapper>
              <img src={cat2} alt='Dancing cat' />
            </S.ImgWrapper>
            <S.ImgWrapper>
              <img src={cat1} alt='Dancing cat' />
            </S.ImgWrapper>
            <S.ImgWrapper>
              <img src={cat3} alt='Dancing cat' />
            </S.ImgWrapper>
          </S.ImagesWrapper>
          <S.EzhikWrapper>
            <img src={ezhik} alt='Ezhik' />
            <S.Button onClick={handleClick} />
          </S.EzhikWrapper>
        </S.Wrapper>
      </S.Container>
    </S.Section>
  );
};

export default App;
